.ContainerHeader {
    width: 100vw;
    display: flex;
    flex-direction: row-reverse;
}

.Title {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    margin: 2vw 2% 0 2vw;
}

.Title p {
    font-size: 1.5rem;
    font-weight: 700;
}

.LineHeader {
    width: 100%;
    height: 3px;
    background-color: black;
}

.Logo {
    display: flex;
    justify-content: flex-end;
    width: 50%;
    margin: 1vw 2% 0 2vw;
}

.Logo img {
    width: 30%;
}

@media (max-width: 920px) {
    .Logo img {
        width: 38%;
    }
}

@media (max-width: 850px) {
    .Title p {
        font-size: 1.2rem;
    }
}

@media (max-width: 750px) {
    .Title p {
        font-size: 1.1rem;
    }
    .Logo img {
        width: 40%;
    }
}