.Container {
    margin: 2vw 2% 7% 2vw;

    display: flex;
    flex-direction: column;
}

.FirstSection {
    width: 100%;

    display: flex;
}

.Cfr {
    width: 50%;

    text-align: start;
}

.ContainerTotals {
    width: 50%;
    padding-right: 2%;
}

.Totals {
    display: flex;
    justify-content: space-between;
    text-align: start;
}

.TotalLiters {
    white-space: nowrap;
}

.FormLiters {
    display: flex;
    flex-direction: row-reverse;

    width: 55%;
}
.LitersInput {
    width: 50%;
    border-left: none;
    border-right: none;
    border-bottom: 1px solid black;

    margin-left: 0.5vw;
}


.Comments {
    text-align: start;
}